export function detectLanguage() {
    const navigatorLanguage = navigator.language || navigator.userLanguage;
    const languageCode = navigatorLanguage.split('-')[0]; // Prend seulement la partie principale du code de langue
  
    // Liste des langues supportes par votre application
    const supportedLanguages = ['fr', 'en'];
  
    // Vrifie si la langue dtecte est supporte, sinon utilise l'anglais par dfaut
    return supportedLanguages.includes(languageCode) ? languageCode : 'en';
  }